.profile {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1400px) 1fr;
}

.profile__block {
  grid-column: 2/3;
  padding: 10px;
  margin: 0;
  display: grid;
  grid-template: 1fr 1fr / 1fr minmax(auto, 500px) minmax(auto, 900px) 1fr;
}

.profile__block span {
  font-weight: bold;
  padding-bottom: 10px;
}

.block__data {
  grid-column: 2/3;
  grid-row: 1/2;

  background-color: rgb(245, 245, 245);
  text-align: center;
  padding: 10px;
}

.data__photo {
  text-align: center;
}

.data__photo img {
  width: 300px;
  height: 100%;
}

.data__title {
  display: grid;
  text-align: center;
  font-size: 25px;
  font-weight: 800;
}

.block__form {
  grid-column: 3/4;
  grid-row: 1/2;

  padding-left: 10px;
  background-color: rgb(245, 245, 245);
  margin-left: 20px;
  text-align: left;
  padding: 10px;
}

.form__block {
  padding-left: 10px;
  color: rgb(77, 77, 77);
}

.block__wall {
  grid-column: 3/4;
  grid-row: 2/3;

  padding-left: 10px;
  background-color: rgb(245, 245, 245);
  margin-left: 20px;
  margin-top: 20px;
  padding: 10px;
}

.wall__photo img {
  border-radius: 30px;
}

.general__subscribe,
.general__button {
  color: white;
  background-color: black;
  margin: 10px 3px;
}
.general__subscribe:hover,
.general__button:hover {
  transform: scale(1.1);
  color: rgb(147, 109, 76);
}

@media (max-width: 768px) {
  .profile__block {
    padding: 0px;
    margin: 5px;
    display: grid;
    grid-template: minmax(auto, 400px) minmax(auto, auto) minmax(100px, auto) / 1fr minmax(
        auto,
        600px
      ) 1fr;
  }

  .block__data {
    grid-column: 2/3;
    grid-row: 1/2;

    background-color: rgb(255, 255, 255);
    text-align: center;
    padding: 0px;
  }

  .data__photo img {
    width: 300px;
    height: 300px;
  }

  .data__title {
    display: grid;
    text-align: center;
    font-size: 14px;
    font-weight: 800;
  }

  .block__form {
    grid-column: 2/3;
    grid-row: 2/3;

    padding-left: 10px;
    background-color: rgb(255, 255, 255);
    margin-left: 0px;
    text-align: center;
    padding: 0px;
    font-size: 11px;
    padding-left: 6px;
  }

  .form__block {
    padding-left: 10px;
    color: rgb(77, 77, 77);
  }

  .block__wall {
    text-align: center;
    grid-column: 2/3;
    grid-row: 3/4;
    padding-left: 0px;
    background-color: rgb(255, 255, 255);
    margin-left: 0px;
    margin-top: 0px;
    padding: 0px;
    padding-left: 6px;
  }

  .wall__input {
    font-size: 12px;
  }

  .general__subscribe,
  .general__button {
    color: white;
    background-color: black;
    margin: 10px 3px;
  }
  .general__subscribe:hover,
  .general__button:hover {
    transform: scale(1.1);
    color: rgb(147, 109, 76);
  }

  .data__status {
    max-width: 200px;
  }
}
